<template>
  <div>
   <div>时间： <da-time-select v-model="MonitoringTime" :show-time="[1,0]" @change="changeMonitoringTime"></da-time-select>
        所属部门：<da-radio-select v-model="department" :maxNum="1" :option-lists="departmentList" label="name" value-key="id" @change="changeDepartment"></da-radio-select>
        设备类型：<da-radio-select v-model="equipmentType" :maxNum="1" :option-lists="equipmentTypeList" label="name" value-key="id" @change="changeEquipmentType"></da-radio-select>
        设备：<da-radio-select v-model="equipment" :maxNum="1" :option-lists="equipmentList" label="name" value-key="id" @change="changeEquipment"></da-radio-select> 
    </div>
    <da-panel title="设备运行状态监测"  width="100%" height="auto" min-height="200px" padding="0 10px" ref="panelCount">
      
    </da-panel>
    <da-panel title="设备启停状态统计"  width="100%" height="auto" min-height="200px" padding="0 10px" ref="panelCount">
      <!-- equptStatusTable -->
      本月<alarm-data :dataLists="equptStatusMon" label="name" value-key="id" mode="horizontal"></alarm-data>
      本年<alarm-data :dataLists="equptStatusYear" label="name" value-key="id" mode="horizontal"></alarm-data>
    </da-panel>
    <da-panel title="同类设备本月停机次数占比"  width="100%" height="auto" min-height="200px" padding="0 10px" ref="panelCount">
      <da-pie-chart id="equptStopPpt" title=timeNow :data-lists="equptStopPptData"></da-pie-chart>
    </da-panel>
    <da-panel title="同类设备本月停机时长占比"  width="100%" height="auto" min-height="200px" padding="0 10px" ref="panelCount">
      <da-pie-chart id="equptStopTime" title=timeNow :data-lists="equptStopTimeData"></da-pie-chart>
    </da-panel>
  </div>
</template>

<script>
import alarmData from "../template/alarmData";
export default {
  data() {
    return {
      MonitoringTime: "",
      department: "",
      departmentList: [],
      equipmentType: "",
      equipmentTypeList: [],
      equipment: "",
      equipmentList: [],
      equptStatusMon: "",
      equptStatusYear: "",
      equptStopPptData: [],
      equptStopTimeData: []
    };
  },
  methods: {
    inintData() {},
    loadData() {
      this.inintData();
      this.getDepartmentList();
      this.getEquipmentTypeList();
      this.getEquipmentList();
      this.getEquptStatusMon();
      this.getEquptStatusYear();
      this.getEquptStopPptData();
      this.getEquptStopTimeData();
    },
    changeMonitoringTime() {},
    getDepartmentList() {},
    changeDepartment() {},
    getEquipmentTypeList() {},
    changeEquipmentType() {},
    getEquipmentList() {},
    changeEquipment() {},
    getEquptStatusMon() {},
    getEquptStatusYear() {},
    getEquptStopPptData() {},
    getEquptStopTimeData() {}
  },
  mounted() {
    this.loadData();
  }
};
</script>


